import Vue       from 'vue'
import Router    from 'vue-router'
import AddRecord from './views/AddRecord.vue'

Vue.use( Router )

export default new Router( {
  routes: [
    {
      path: '/',
      name: 'Add Record',
      component: AddRecord
    },
    {
      path: '/showlog',
      name: 'Show Log',
      component: ( ) => import('./views/ShowLog.vue')
    },
    {
      path: '/signup',
      name: 'Sign Up',
      component: ( ) => import('./views/SignUp.vue')
    }
  ]
} )
