<template>
  <div class="mt-5">
    <form action="api/exercise/add" @submit.prevent="addLog">
      <div class="form-group row">
        <div class="input-group">
          <div class="input-group-prepend col-sm-4 col-md-2 px-0">
            <label for="username" class="input-group-text w-100">Username</label>
          </div>
          <input
            id="username"
            class="col-sm-8 col-md-10 form-control"
            type="text"
            v-model="username"
            @input="resetAlerts"
            placeholder="Your username, please"
            min="3"
            max="30"
            required
            autofocus
          />
        </div>
      </div>
      <div class="form-group row">
        <div class="input-group">
          <div class="input-group-prepend col-sm-4 col-md-2 px-0">
            <label for="name" class="input-group-text w-100">Description</label>
          </div>
          <input
            id="name"
            class="col-sm-8 col-md-10 form-control"
            v-model="name"
            type="text"
            @input="resetAlerts"
            placeholder="What you want to add to your log"
            max="100"
            required
          />
        </div>
      </div>
      <div class="form-group row">
        <div class="input-group">
          <div class="input-group-prepend col-sm-4 col-md-2 px-0">
            <label for="time" class="input-group-text w-100">Duration</label>
          </div>
          <input
            id="time"
            class="col-sm-8 col-md-10 form-control"
            v-model="time"
            type="number"
            @input="resetAlerts"
            placeholder="Time in minutes"
            max="1440"
            required
          />
        </div>
      </div>
      <div class="form-group row">
        <div class="input-group">
          <div class="input-group-prepend col-sm-4 col-md-2 px-0">
            <label for="date" class="input-group-text w-100">Date</label>
          </div>
          <datepicker
            id="date"
            wrapper-class="col-sm-8 col-md-10 px-0"
            input-class="w-100 form-control"
            v-model="date"
            @input="resetAlerts"
            placeholder="The date on which you did it"
            typeable
          />
        </div>
      </div>
      <div class="form-group row mt-5">
        <div class="col-sm-12 text-center mx-0 px-0">
          <button type="submit" class="btn btn-block btn-primary">Save to Log</button>
        </div>
      </div>
      <Alert :output="output" :error="error" />
    </form>
  </div>
</template>

<script>
import Alert      from './Alert';
import Datepicker from 'vuejs-datepicker';
import axios      from 'axios';

export default {
  name: 'AddRecordForm',
  components: {
    Datepicker,
    Alert
  },
  data() {
    return {
      username: '',
      name    : '',
      time    : '',
      date    : new Date( ),
      output  : '',
      error   : ''
    }
  },
  methods: {
    resetAlerts ( ) {
      this.output = this.error = this.results = null; 
    },
    addLog( e ) {
      e.preventDefault( );
      this.resetAlerts( );
      const query = {
        username: this.username,
        name    : this.name,
        time    : this.time,
        date    : this.date
      }
      axios
        .post( `/api/exercise/add`, query )
        .then( data => {
          this.output = data.data;
        } )
        .catch( error => {
          this.error = error.response.data.error;
        } );
    }
  }
}
</script>
