<template>
  <transition name="fade">
    <div v-show="output || error">
      <div
        v-html="output ? output : error"
        :class="output ? 'alert alert-success' : 'alert alert-danger'"
        role="alert">
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'Alert',
  props: [ 'output', 'error' ]
}
</script>
